@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

h1, h2, h3, h4, h5, h6, p, span {
  cursor: default; }

.shop-page {
  padding-top: 16px;
  .page {
    display: flex; } }


.shop-page-sidebar {
  width: 330px;
  min-width: 330px;
  padding: 0 15px;
  box-sizing: content-box;
  @media screen and (max-width: 950px) {
    display: none; } }

.shop-page-content {
  width: calc(100% - 370px);
  padding: 0 15px;
  box-sizing: content-box;
  @media screen and (max-width: 950px) {
    width: 100%; }
  @media screen and (max-width: 480px) {
    padding: 0; } }

.catalog-overlay {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  background: rgba(0,0,0,.54); }

.shop-content {
  background-color: #fff;
  border-radius: 8px;
  padding: 14px 20px;
  position: relative;
  h2 {
    font-size: 24px;
    font-weight: 500;
    color: $dark-color;
    line-height: 1.5;
    font-family: 'Roboto', sans-serif;
    margin-left: 5px; }
  .filters-btn {
    height: 36px;
    background-color: #fff;
    border: 1px solid #ddd;
    color: $dark-color;
    border-radius: 5px;
    max-width: 200px;
    text-align: center;
    padding: 5px 40px;
    position: absolute;
    right: 40px;
    top: 14px;
    display: none;
    transition: all .3s ease;
    &:hover {
      border: 1px solid $main-color; } }
  @media screen and (max-width: 950px) {
    padding-right: 5px;
    .filters-btn {
      display: block; } }
  @media screen and (max-width: 600px) {
    padding: 15px;
    h2 {
      max-width: 50%; }
    .filters-btn {
      right: 15px; } }
  @media screen and (max-width: 480px) {
    padding: 15px 5px; }
  @media screen and (max-width: 375px) {
    padding: 15px 10px;
    h2 {
      max-width: 100%;
      margin-left: 0; }
    .filters-btn {
      left: 10px;
      top: 60px; } } }

.dropdown-hidden {
  margin-top: -16px; }

.items-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-top: 20px;
  @media screen and (max-width: 950px) {
    margin-top: 30px; }
  @media screen and (max-width: 375px) {
    margin-top: 70px; } }

.sidenav-right {
  position: absolute;
  right: 0;
  width: 320px;
  max-width: 320px;
  background-color: #fff;
  top: 0;
  padding-top: 30px;
  padding-left: 10px;
  padding-right: 10px;
  z-index: 15;
  border-bottom-left-radius: 5px;
  animation: sliding-left .3s linear;
  @media screen and (max-width: 400px) {
    width: 300px; }
  .close-sidenav {
    height: 12px;
    width: 12px;
    background: url('../../assets/cancel.svg') no-repeat;
    position: absolute;
    left: 8px;
    top: 10px;
    cursor: pointer;
    transition: all .3s ease;
    &:hover {
      transform: rotate(90deg); } } }

@keyframes sliding-left {
  0% {
    right: -160px; }
  25% {
    right: -120px; }
  50% {
    right: -80px; }
  75% {
    right: -40px; }
  100% {
    right: 0; } }

.sidenav-left {
  margin-top: -16px;
  display: none;
  @media screen and (max-width: 950px) {
    display: block; } }
