.carousel-wrapper {
  margin-top: 12px; }

.carousel {
  width: 100%;
  height: 500px;
  background-color: rgba(43, 194, 217, 0.5);
  overflow: hidden;
  border-radius: 5px; }

.car-item {
  background-color: red; }

.carousel {
  .slide {
    padding: 0 !important; }
  .control-dots .dot {
    &.selected, &:hover {
      padding: 0 !important; }
    &:focus {
      outline: none !important; } } }
