@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

.categories {
  width: 100%;
  background-color: #fff;
  box-sizing: border-box;
  padding: 30px 28px 30px 5px;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  font-family: 'Roboto', sans-serif;
  color: $dark-color;
  margin-bottom: 15px;
  ul {
    padding: 0;
    li {
      list-style-type: none;
      padding-left: 71px;
      font-size: 18px;
      padding-top: 10px;
      padding-bottom: 10px;
      color: $dark-color;
      transition: color .2s ease;
      position: relative;
      &:hover {
        background-color: #ededed;
        border-radius: 5px;
        a {
          color: $main-color;
          position: relative;
          &:after {
            content: '';
            position: absolute;
            top: -10px;
            bottom: -10px;
            left: -70px;
            right: -170px; } } }
      &::before {
        content: '';
        width: 24px;
        height: 24px;
        display: block;
        position: absolute;
        left: 23px;
        top: 10px; }
      &:nth-child(1) {
        &::before {
          background: url('../../assets/iphone.svg') no-repeat;
          width: 26px;
          height: 26px;
          top: 8px; } }
      &:nth-child(2) {
        &::before {
          background: url('../../assets/laptop.svg') no-repeat;
          top: 14px; } }
      &:nth-child(3) {
        &::before {
          background: url('../../assets/apple-watch.svg') no-repeat;
          top: 9px; } }
      &:nth-child(4) {
        &::before {
          background: url('../../assets/headphones.svg') no-repeat;
          top: 12px;
          left: 26px; } }
      &:nth-child(5) {
        &::before {
          background: url('../../assets/pulse.svg') no-repeat;
          left: 24px; } }
      &:nth-child(6) {
        &::before {
          background: url('../../assets/monitor.svg') no-repeat; } }
      &:nth-child(7) {
        &::before {
          background: url('../../assets/cable.svg') no-repeat;
          left: 25px; } } } } }
