@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

.favs-item {
  list-style-type: none;
  display: flex;
  padding: 12px 20px;
  align-items: center;
  border-radius: 5px;
  border: 1px solid #fff;
  transition: all .3s ease;
  &:hover {
    border: 1px solid #ddd; } }

.preview-actions {
  display: flex;
  .remove-fav {
    margin: auto 5px;
    width: 20px;
    height: 20px;
    display: block;
    background: url('../../assets/like-hover.svg') no-repeat;
    &:hover {
      background: url('../../assets/like.svg') no-repeat; } } }

.fav-buy {
  margin-left: 25px;
  background-color: $main-color;
  border-radius: 5px;
  height: 30px;
  width: 170px;
  text-align: center;
  font-size: 14px;
  line-height: 1.5;
  color: #fff;
  transition: all .3s ease;
  &:hover {
    background-color: #2DB32D; } }
