@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

.sidebar {
  background-color: #fff;
  width: 100%;
  border-radius: 8px;
  padding: 25px;
  border: solid 1px #ddd;
  font-family: 'Roboto', sans-serif;
  margin-bottom: 10px; }

.selected-header {
  position: relative;
  margin-bottom: 10px;
  display: flex;
  h3 {
    font-size: 20px;
    font-weight: 500;
    color: #404040;
    line-height: 2; }
  .delete-all-selected {
    height: 20px;
    margin-top: 10px;
    margin-left: auto;
    position: relative;
    width: 100px;
    background-color: #fff;
    padding: 0;
    span {
      font-weight: 400;
      font-size: 13px;
      margin-left: 18px;
      line-height: 13px;
      color: $dark-color;
      transition: all .5s ease;
      &::before {
        content: '';
        background: url('../../assets/cancel-small.svg') no-repeat;
        width: 11px;
        height: 11px;
        display: block;
        position: absolute;
        left: 0;
        top: 7px; } }
    &:hover {
      span {
        color: red; } } } }

.selected-item {
  display: inline-block;
  height: 32px;
  border: dashed 1px $main-color;
  border-radius: 16px;
  line-height: 30px;
  padding: 0 16px;
  position: relative;
  cursor: default;
  margin-bottom: 14px;
  span {
    font-size: 14px;
    display: inline-block;
    color: $dark-color;
    &:first-child {
      color: #777; }
    &:nth-child(2) {
      margin-left: 6px; } }
  .delete-selected {
    height: 12px;
    width: 12px;
    background: url('../../assets/cancel.svg') no-repeat;
    margin-left: 12px; } }

