.search-relative {
  position: relative; }

.search-input {
  width: 100%;
  background-color: #888;
  padding-left: 20px;
  height: 39px;
  border: none;
  border-radius: 5px;
  color: #fff;
  position: relative;
  &::placeholder {
    color: #fff;
    font-size: 18px;
    font-family: 'Roboto', sans-serif; }
  &:focus {
    outline: none;
    border: none transparent; } }

.search-icon {
  background: url('../../assets/search.svg') no-repeat;
  width: 20px;
  height: 20px;
  display: block;
  position: absolute;
  top: 10px;
  right: 30px;
  @media screen and (max-width: 600px) {
    display: none; } }

.btn-clear {
  border: none;
  &:hover {
    border: none; } }
