@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

.error-page {
  background-color: rgba(51, 204, 51, 0.08);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-wrap: wrap;
  font-family: 'Roboto', sans-serif;
  .error-content {
    text-align: center;
    h1 {
      width: 100%;
      color: $dark-color;
      font-size: 36px;
      cursor: default; }
    .error-btn {
      background-color: $main-color;
      width: 200px;
      border: solid 1px $main-color;
      color: #fff;
      border-radius: 5px;
      height: 40px;
      margin-top: 15px;
      font-size: 18px;
      line-height: 1.5;
      font-weight: 500;
      transition: all .3s ease;
      &:hover {
        background-color: #2DB32D; } } } }
