@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

.card {
  background-color: #fff;
  border-radius: 10px;
  height: 400px;
  width: 231px;
  padding: 20px;
  border: solid 1px #ddd;
  margin-right: 15px;
  margin-left: 5px;
  margin-bottom: 20px;
  transition: all .3s ease;
  &:hover {
    border: 1px solid $main-color; } }

.img-wrapper {
  max-width: 100%;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%; } }

.card-body {
  font-family: 'Roboto', sans-serif;
  padding: 5px 5px 8px;
  flex: none;
  height: 90px;
  cursor: pointer;
  a {
    font-size: 17px;
    text-decoration: none;
    color: $dark-color;
    font-weight: 400; }
  &:hover a {
    text-decoration: underline;
    text-decoration-color: $dark-color; } }

.price {
  font-size: 18px;
  font-weight: 400;
  color: $dark-color;
  height: 32px;
  padding: 0 5px;
  margin-bottom: 8px;
  cursor: default;
  display: block; }

.card-info {
  padding-top: 14px;
  display: inline-block;
  border-top: solid 1px #ddd; }

.isExist {
  font-size: 14px;
  line-height: 1.5;
  float: left;
  color: $main-color;
  margin-top: -2px;
  cursor: default;
  img {
    margin-bottom: 3px; }
  span {
    margin-left: 5px;
    display: inline-block; } }

.small-buy {
  float: right;
  background-color: $main-color;
  border-radius: 5px;
  height: 25px;
  width: 80px;
  text-align: center;
  font-size: 14px;
  line-height: 1.5;
  color: #fff;
  margin-top: -3px;
  transition: all .3s ease;
  &:hover {
    background-color: #2DB32D; } }


.fav-check {
  width: 18px;
  height: 18px;
  display: block;
  background: url('../../assets/like.svg') no-repeat;
  position: absolute;
  top: 10px;
  right: 10px;
  &:hover {
    background: url('../../assets/like-hover.svg') no-repeat; } }

.fav-selected {
  background: url('../../assets/like-hover.svg') no-repeat; }

@media screen and (max-width: 600px) {
  .card {
    width: 50%;
    margin: 0 0 10px;
    padding: 30px 10px 10px; }
  .card-body a {
    font-size: 16px; } }

@media screen and (max-width: 480px) {
  .card {
    padding: 30px 7px 7px; }
  .card-body a {
    font-size: 14px; }
  .price {
    font-size: 16px; } }

@media screen and (max-width: 410px) {
  .card {
    height: 100%; }
  .card-info {
    padding-top: 5px; }
  .isExist {
    float: none;
    text-align: center; }
  .small-buy {
    width: 100%;

    margin-top: 5px; } }

@media screen and (max-width: 370px) {
  .card {
    width: 100%; }
  .card-body {
    height: 80px;
    a {
      font-size: 16px; } }
  .price {
    font-size: 18px; } }


