$main-color: #33cc33;
$dark-color: #404040;

.admin-content {
  padding-top: 20px;
  .admin-home {
    padding: 5px 15px;
    border-radius: 10px;
    background: #fff;
    border: solid 1px #ccc;
    transition: all .5s ease;
    a {
      color: $dark-color;
      font-size: 16px; }
    &:hover {
      border: solid 1px $main-color; } } }

.add-item {
  margin-top: 30px;
  margin-left: 20%;
  select {
    border: dashed 1px $main-color;
    border-radius: 5px;
    width: 185px;
    height: 36px;
    color: #777;
    font-size: 14px;
    font-weight: 500;
    padding-left: 18px;
    padding-right: 4px;
    margin-bottom: 16px;
    transition: all .3s ease;
    &:focus {
      border: solid 1px $main-color;
      outline: none; } }
  .btn-add {
    float: right;
    margin-top: -10px;
    margin-right: 20px; } }

.display-items {
  margin-top: 30px; }
