@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

main {
  font-family: 'Roboto', sans-serif; }

.item-navigation {
  height: 65px;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 28px;
  .breadcrumbs {
    font-size: 13px;
    ul {
      padding: 0;
      margin-bottom: 0;
      li {
        display: inline-block;
        line-height: 1.5;
        margin-right: 24px;
        position: relative;
        &:not(:last-child)::before {
          content: '';
          position: absolute;
          width: 8px;
          height: 8px;
          display: block;
          background: url('../../assets/right-arrow.svg') no-repeat;
          bottom: 5px;
          right: -16px; }
        &:first-child {
          bottom: 4px;
          &::before {
            bottom: 1px; } } } } }
  .item-code {
    margin-left: auto;
    font-size: 15px; } }

.item-content {
  background-color: #fff;
  border-radius: 5px;
  padding: 20px 10px;
  min-height: 750px;
  .img-wrapper {
    height: 660px;
    img {
      transition: all .5s ease-out; }
    &:hover {
      img {
        transform: scale(1.04); } } }
  .item-text {
    padding: 10px 20px 10px 40px;
    h1 {
      font-size: 36px;
      color: $dark-color; }
    .options-picker {
      margin-top: 45px;
      margin-bottom: 60px;
      display: flex;
      p {
        font-size: 16px;
        color: $dark-color; }
      .model-picker {
        margin-left: 60px; } }
    .price {
      font-size: 26px;
      font-weight: 500;
      color: $dark-color; }
    .buttons-row {
      margin-top: 35px;
      margin-bottom: 25px;
      display: flex;
      .buy-success {
        background-color: $main-color;
        width: 300px;
        border: solid 1px $main-color;
        color: #fff;
        border-radius: 5px;
        height: 46px;
        margin-right: 9px;
        font-size: 18px;
        line-height: 1.5;
        font-weight: 500;
        text-transform: uppercase;
        transition: all .3s ease;
        &:hover {
          background-color: #2DB32D; } }
      .buy-secondary {
        background-color: #fff;
        width: 220px;
        border: solid 1px $main-color;
        color: $main-color;
        border-radius: 5px;
        height: 46px;
        margin-right: 18px;
        font-size: 18px;
        line-height: 1.5;
        font-weight: 500;
        text-transform: uppercase;
        transition: all .3s ease;
        &:hover {
          background-color: rgba(51, 204, 51, 0.05); } }
      .favs-add {
        display: block;
        width: 18px;
        height: 18px;
        background: url('../../assets/like.svg') no-repeat;
        margin-top: 14px;
        &:hover {
          background: url('../../assets/like-hover.svg') no-repeat; } } }
    .divider {
      width: 100%;
      height: 1px;
      background-color: #ececec; }
    h3 {
      font-size: 18px;
      font-weight: 500;
      color: $dark-color;
      margin-bottom: 22px;
      margin-top: 16px;
      line-height: 1; } } }

.fast-buy {
  span {
    font-size: 14px;
    line-height: 1.5;
    color: $dark-color;
    margin-bottom: 8px;
    display: block;
    font-weight: 500;
    cursor: default; }
  input {
    width: 185px;
    border: dashed 1px $main-color;
    background-color: #fff;
    color: #777;
    border-radius: 5px;
    display: block;
    margin-bottom: 16px;
    font-weight: 500;
    font-size: 14px;
    padding-left: 18px;
    padding-right: 10px;
    height: 36px;
    transition: all .5s ease;
    &::placeholder {
      color: #777;
      font-size: 14px;
      font-weight: 500; }
    &:focus {
      outline: none;
      border: solid 1px $main-color; } } }

.buy-now {
  width: 250px;
  margin: 4px 0;
  background-color: $main-color;
  border: solid 1px $main-color;
  color: #fff;
  border-radius: 5px;
  height: 46px;
  font-size: 18px;
  line-height: 1.5;
  font-weight: 500;
  transition: all .3s ease;
  &:hover {
    background-color: #2DB32D; } }

//options-picker

.custom-color-picker {
  div {
    display: inline-block;
    margin-right: 14px; }

  input[type="radio"] {
    display: none;

    + label {
      color: $dark-color;
      font-family: 'Roboto', sans-serif;
      font-size: 14px;

      span {
        display: inline-block;
        width: 25px;
        height: 25px;
        vertical-align: middle;
        cursor: pointer;
        box-sizing: content-box;
        border: 1px solid #ececec;
        border-radius: 5px;
        background-repeat: no-repeat;
        background-position: center;
        text-align: center;
        line-height: 25px;
        transition: all .3s ease; } }

    &#color-1 + label span {
      background-color: #F8E5C4; }

    &#color-2 + label span {
      background-color: #5E5D62; }

    &#color-3 + label span {
      background-color: #C0C0C0; }

    &:checked + label span {
      border: 1px solid $main-color; } } }

.custom-model-picker {
  div {
    display: inline-block;
    margin-right: 10px; }

  input[type="radio"] {
    display: none;

    + label {
      color: $dark-color;
      font-family: 'Roboto', sans-serif;
      font-size: 14px;

      span {
        display: inline-block;
        width: 70px;
        height: 28px;
        vertical-align: middle;
        cursor: pointer;
        border: 1px solid #efefef;
        border-radius: 5px;
        box-sizing: content-box;
        background-repeat: no-repeat;
        background-position: center;
        text-align: center;
        line-height: 28px;
        transition: all .3s ease; } }

    &#color-4 + label span {
      background-color: #fff; }

    &#color-5 + label span {
      background-color: #fff; }

    &:checked + label span {
      border: 1px solid $main-color; } } }
