@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

h2 {
  font-size: 24px;
  font-weight: 500;
  color: $dark-color;
  line-height: 1.5;
  font-family: 'Roboto', sans-serif;
  margin-left: 5px;
  margin-bottom: 20px; }

.cart-modal-window {
  padding: 10px;
  color: $dark-color;
  line-height: 1.5;
  font-family: 'Roboto', sans-serif;
  min-width: 500px;
  min-height: 100px;
  .blank-total {
    text-align: center;
    font-size: 20px;
    margin-top: 30px;
    color: $main-color; }
  .total {
    position: relative;
    margin-top: 30px;
    margin-right: 5px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-size: 16px;
    .cart-btn {
      width: 230px;
      height: 40px; }
    span {
      margin-right: 15px;
      font-weight: 500; } } }

.cart-item {
  list-style-type: none;
  display: flex;
  padding: 12px 20px;
  align-items: center;
  border-radius: 5px;
  border: 1px solid #fff;
  transition: all .3s ease;
  &:hover {
    border: 1px solid $main-color; } }

.preview-img {
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: auto;
    max-width: 100%;
    height: auto;
    max-height: 100%; } }

.preview-body {
  position: relative;
  padding-left: 15px;
  display: flex;
  .preview-text {
    width: 285px;
    margin-right: 15px;
    a {
      font-size: 15px;
      cursor: default; } }
  .preview-counter {
    min-width: 45px;
    margin-right: 15px;
    display: flex;
    align-items: center; }
  .preview-price {
    min-width: 95px;
    margin-right: 15px;
    display: flex;
    align-items: center;
    cursor: default; } }

.preview-actions {
  display: flex;
  .preview-action {
    display: block;
    width: 22px;
    height: 22px;
    margin: auto 3px; }
  .ondecrease {
    background: url('../../assets/minus.svg') no-repeat;
    &:hover {
      background: url('../../assets/minus-hover.svg') no-repeat; } }
  .onincrease {
    background: url('../../assets/plus.svg') no-repeat;
    &:hover {
      background: url('../../assets/plus-hover.svg') no-repeat; } }
  .ondelete {
    background: url('../../assets/error.svg') no-repeat;
    &:hover {
      background: url('../../assets/error-hover.svg') no-repeat; } } }
