@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

button {
  outline: none;
  &:active, &:focus {
    outline: none; } }

input {
  outline: none;
  &:active, &:focus {
    outline: none; } }

.row {
  margin: 0; }

main {
  font-family: 'Roboto', sans-serif; }

.page-wrapper {
  background-color: #ececec;
  width: 100%;
  height: 100%;
  padding-bottom: 90px;
  overflow-x: hidden;
  .page {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto; } }

.items-row {
  padding: 14px 20px;
  margin-top: 40px;
  background-color: #fff;
  border-radius: 8px;
  h3 {
    font-size: 24px;
    line-height: 2;
    color: $dark-color;
    margin-left: 5px; }
  .items-in-row {
    display: flex;
    justify-content: space-between;
    margin-top: 20px; } }

@media screen and (max-width: 780px) {
  .small-delete {
    display: none; } }
