@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

header {
  width: 100%;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  color: $dark-color; }

a {
  color: $dark-color;
  transition: all .4s ease;
  &:hover {
    text-decoration: none;
    color: $main-color; } }

.stretching {
  background-color: #2bc2d9;
  height: 30px;
  color: #fff;
  font-size: 16px;
  cursor: default;
  display: flex;
  align-items: center;
  justify-content: center;
  @media screen and (max-width: 780px) {
    font-size: 14px; }
  @media screen and (max-width: 480px) {
    display: none; } }

.primary {
  background-color: #fff;
  .inner-primary {
    max-width: 1440px;
    width: 100%;
    height: 65px;
    margin: 0 auto;
    padding: 0 15px;
    display: flex;
    align-items: center;
    position: relative;
    .logo {
      color: #fff;
      background-color: $main-color;
      width: 200px;
      height: 35px;
      margin-left: 28px;
      margin-right: 20px;
      text-align: center;
      border-radius: 8px;
      vertical-align: middle;
      line-height: 2;
      cursor: default; }
    ul {
      margin: 0 60px 0 auto;
      li {
        display: inline-block;
        margin: 0 20px;
        line-height: 1.8; } }
    .contacts {
      font-size: 19px;
      line-height: 1.8;
      font-weight: 700;
      margin-right: 28px;
      a:hover {
        color: $dark-color;
        cursor: default; } } }
  @media screen and (max-width: 950px) {
    display: none; } }

.secondary {
  background-color: #777;
  .secondary-inner {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
    height: 55px;
    display: flex;
    color: #fff;
    align-items: center;
    .header-search {
      margin-left: 30px;
      width: 45vw;
      @media screen and (max-width: 950px) {
        width: 50vw; }
      @media screen and (max-width: 780px) {
        margin-left: 15px;
        width: 100%; }
      @media screen and (max-width: 600px) {
        margin-left: 5px;
        width: 100%; } } } }

.categories-header {
  background-color: #666;
  width: 330px;
  min-width: 330px;
  height: 55px;
  display: flex;
  align-items: center;
  padding: 0 28px;
  font-size: 19px;
  cursor: default;
  transition: all .5s ease;
  &:hover {
    background-color: #555; }
  .burger {
    width: 24px;
    height: 1px;
    background-color: #fff;
    position: relative;
    margin-bottom: 1px;
    &:before {
      content: "";
      position: absolute;
      width: 24px;
      height: 1px;
      background-color: #fff;
      top: 6px; }
    &:after {
      content: "";
      position: absolute;
      width: 24px;
      height: 1px;
      background-color: #fff;
      bottom: 6px; } }
  .title {
    margin-bottom: 1px;
    margin-left: 10px; }
  @media screen and (max-width: 950px) {
    width: 200px;
    min-width: 200px; }
  @media screen and (max-width: 780px) {
    width: 64px;
    min-width: 64px;
    padding: 0 20px;
    background-color: #777;
    &:hover {
      background-color: #666; }
    .title {
      display: none; } }
  @media screen and (max-width: 600px) {
    padding: 0 12px;
    width: 48px;
    min-width: 48px;
    height: 53px; } }


.fc-wrapper {
  margin-left: auto;
  margin-right: 55px;
  @media screen and (max-width: 950px) {
    margin-right: 15px; }
  @media screen and (max-width: 600px) {
    margin-right: 0; } }

.favs-and-cart {
  justify-content: center;
  display: flex;
  .favs {
    background: url('../../assets/heart.svg') no-repeat;
    width: 32px;
    height: 32px;
    display: block;
    margin-left: 35px;
    margin-right: 20px;
    cursor: pointer; }
  .cart {
    background: url('../../assets/shopping-cart.svg') no-repeat;
    width: 32px;
    height: 32px;
    display: block;
    cursor: pointer; }
  @media screen and (max-width: 780px) {
    .favs {
      margin-left: 20px; } }
  @media screen and (max-width: 600px) {
    .favs {
      margin: 0 12px; } } }



.btn-clear {
  border: none;
  &:hover {
    border: none; } }

.modal-close {
  background: url('../../assets/cancel.svg') no-repeat center;
  width: 32px;
  height: 32px;
  display: block;
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer; }

.search-block {
  z-index: 10;
  position: absolute;
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  left: 0;
  right: 0;
  font-family: 'Roboto', sans-serif;
  color: $dark-color;
  .inner-search {
    margin-left: 375px;
    width: 45vw;
    min-width: 560px;
    @media screen and (max-width: 950px) {
      margin: 0 10%;
      width: 80%; }
    @media screen and (max-width: 780px) {
      min-width: auto;
      max-width: 1440px;
      width: 95%;
      margin: 0 auto; }
    @media screen and (max-width: 600px) {
      width: 94%; }
    @media screen and (max-width: 480px) {
      width: 92%; } } }


.search-ul {
  padding: 0;
  background-color: #fefefe;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  box-shadow: 0 1px 3px 0 rgba(0,0,0,0.2), 0 1px 1px 0 rgba(0,0,0,0.14), 0 2px 1px -1px rgba(0,0,0,0.12); }

.search-li {
  list-style-type: none;
  display: flex;
  padding: 5px 15px 10px 25px;
  background-color: #fefefe;
  align-items: center;
  position: relative;
  border: solid 1px #fff;
  transition: all .3s ease;
  border-radius: 5px;
  a:hover {
    color: $dark-color;
    cursor: default; }
  &:hover {
    border: solid 1px $main-color;
    .preview-link {
      display: block; } }
  &:first-child {
    padding-top: 15px;
    .preview-link {
      top: 40px; } }
  .preview-img {
    max-width: 70px; }
  .preview-body {
    padding-left: 30px;
    .preview-text {
      font-size: 15px;
      margin-right: 25px;
      cursor: default; }
    .preview-price {
      margin-right: 0; } }
  @media screen and (max-width: 600px) {
    .preview-img {
      width: 50px;
      height: 50px; }
    .preview-body {
      padding-left: 15px;
      display: flex;
      flex-wrap: wrap;
      .preview-text {
        width: 100%;
        font-size: 14px; }
      .preview-price {
        width: 100%;
        margin-top: 5px; } } }
  @media screen and (max-width: 480px) {
    .preview-body {
      .preview-text {
        font-size: 13px; }
      .preview-price {
        font-size: 14px; } } } }


.preview-link {
  display: none;
  transition: all .3s ease;
  background: url('../../assets/right-arrow-long.svg') no-repeat center;
  width: 20px;
  height: 20px;
  position: absolute;
  right: 20px;
  top: 30px;
  cursor: pointer; }
