@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

.sidebar {
  background-color: #fff;
  width: 100%;
  border-radius: 8px;
  padding: 25px;
  border: solid 1px #ddd; }

.filters-header {
  position: relative;
  margin-bottom: 30px;
  h3 {
    margin-left: 40px;
    font-size: 20px;
    font-weight: 500;
    color: #404040;
    line-height: 2;
    &::before {
      content: '';
      background: url('../../assets/filter-results-button.svg') no-repeat;
      width: 24px;
      height: 25px;
      display: block;
      position: absolute;
      left: 0;
      top: 7px; } } }


.side-box {
  border-bottom: solid 1px #ccc;
  margin-bottom: 20px;
  padding-bottom: 20px; }

.sidebar h3 {
  font-size: 20px;
  font-weight: 500;
  color: #404040;
  line-height: 2;
  margin-bottom: 16px; }

.form {
  margin: 0 auto;
  padding: 30px 10px 30px; }

.input-range__track--active {
  background: $main-color !important; }

.input-range__slider {
  background: $main-color !important;
  border: 1px solid $main-color !important; }

.roww {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px; }

.half {
  align-items: center;
  display: flex;
  width: 47%;
  span {
    padding-right: 10px; } }

.el-input {
  display: inline-block;
  font-size: 14px;
  position: relative;
  width: 100%; }

.el-input-inner {
  color: #484748;
  font-size: 16px;
  padding: 16px 11px;
  border: 1px solid #e1e1e1;
  border-radius: 4px;
  box-shadow: inset 0 2px 1px 0 rgba(0, 0, 0, 0.1);
  display: inline-block;
  height: 30px;
  width: 100%; }

label {
  margin-bottom: 0; }

input {
  &[type='number'] {
    -moz-appearance: textfield; }
  &::-webkit-outer-spin-button, &::-webkit-inner-spin-button {
    -webkit-appearance: none; } }

.unique-button {
  font-size: 12px;
  padding: 4px 12px;
  background-color: white;
  border: solid 1px #ccc;
  border-radius: 8px;
  color: $dark-color;
  transition: all .3s ease;
  &:hover {
    border: 1px solid $main-color; } }

.side-box {
  .unique-button {
    float: right;
    margin-top: 5px; }
  .form {
    margin-top: 45px; } }

.maxPrice {
  font-size: 10px;
  padding: 1px 5px;
  background-color: white;
  border: solid 1px black;
  border-radius: 20px;
  display: inline-block;
  color: black; }

//

.checkbox {
  position: relative;
  height: 40px;
  display: flex;
  align-items: center;
  label {
    font-size: 15px;
    color: $dark-color;
    display: flex;
    align-items: center; }
  input {
    display: block;
    width: 22px;
    height: 22px;
    border-radius: 5px;
    border: solid 1px #999;
    margin-right: 15px; } }
